import { Component, OnInit, Injectable } from '@angular/core';
import { ApiService } from '../services/api.service';
import { FormControl, Validators, FormGroup, AsyncValidator, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { debounceTime, distinctUntilChanged, filter, switchMap, map, catchError } from 'rxjs/operators';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  signupForm: FormGroup; 
  private signupErrorMessage: string;

  constructor(private api: ApiService,
              private router: Router,
            private auth: AuthService) { 
    this.signupForm = new FormGroup({
      name: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [this.checkIsNameExit(this.api)],
        updateOn: 'blur'
      }),
      email: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [this.checkIsEmailExit(this.api)],
        updateOn: 'blur'
      }),
      password: new FormControl(null, Validators.required)
    }); 
    this.signupErrorMessage = null;
  }

  ngOnInit() {} 

  signup(){   
      this.auth.signup(this.signupForm.value);
  } 

  checkIsNameExit(api: ApiService){
    return (ctrl: AbstractControl) => api.get('/user', '/isNameExist', {name: ctrl.value}).pipe(
      map(isExist => (isExist ? { nameExist: true } : null)),
      catchError(() => null) 
    ); 
  }

  checkIsEmailExit(api: ApiService){
    return (ctrl: AbstractControl) => this.api.get('/user', '/isEmailExist', {email: ctrl.value}).pipe(
      map(isExist => (isExist ? { emailExist: true } : null)),
      catchError(() => null) 
    ); 
  } 

}
 

 
